.container-empezar {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(3, auto);
  border-radius: 10px;
  overflow: hidden;
  padding: 100px 0px 0px 0px;
}
.container-empezar_cta {
  grid-area: 2/1/3/3;
  z-index: 2;
  margin-left: 50px;
}
.container-empezar_logo {
  height: 34px;
}
.container-empezar_description {
  font-size: 60px;
  font-weight: bold;
  line-height: 70px;
  margin-bottom: 34px;
}
@media (max-width: 991px) {
  .container-empezar_logo {
    height: 34px;
    left: 50%;
    position: relative;
    transform: translateX(-50%);
  }
  .container-empezar_description {
    font-size: 44px;
    font-weight: bold;
    line-height: 55px;
    margin-bottom: 34px;
    text-align: center;
    width: 70%;
    left: 50%;
    position: absolute;
    transform: translateX(-50%);
  }
  .container-empezar_cta {
    grid-area: 1/1/4/5;
    z-index: 2;
    position: relative;
    margin-left: unset;
  }
  .container-empezar_cta .btn_naranja {
    position: absolute;
    bottom: 10px;
    width: 90%;
    left: 50%;
    position: absolute;
    transform: translateX(-50%);
  }
}
.container-empezar_img {
  z-index: 0;
  grid-area: 1/1/4/5;
  justify-self: end;
  border-radius: 0 10px 10px 0;
}
.container-empezar_img_pc {
  grid-area: 1/1/4/5;
  height: 100%;
  display: block;
  justify-self: end;
}
@media (max-width: 991px) {
  .container-empezar_img_pc {
    display: none;
  }
}
.container-empezar_img_movil {
  grid-area: 1/1/4/5;
  justify-self: end;
  height: 100%;
  z-index: 0;
  border-radius: 0 10px 10px 0;
  display: none;
}
@media (max-width: 991px) {
  .container-empezar_img_movil {
    display: block;
    width: 100%;
    height: auto;
    align-self: end;
    justify-self: stretch;
  }
}
@media (min-width: 768px) and (max-width: 991px) {
  .container-empezar_img_movil {
    display: block;
