    height: auto;
    align-self: end;
    justify-self: stretch;
  }
}
@media (max-width: 992px) and (max-width: 1199px) {
  .container-empezar_img_movil {
    display: block;
    width: 100%;
    height: auto;
    align-self: end;
    justify-self: stretch;
  }
}
.container-empezar_overlay {
